/**
 * @typedef {object} bootstrap
 * @desc Module that performs application bootstrapping by registering and initializing **controllers**.
 * @property {function(context: HTMLElement)} initialize - Start application bootstrap process. It bootstraps application controllers within given `HTMLElement`. If `HTMLElement` is not provided, it bootstrap entire application controllers within `document.body`.
 * @property {function(payload: { context: HTMLElement })} run - `initialize` function internally calls `run` which works in similar fashion. It accepts a payload object which contains a context `HTMLElement`.
 * @property {function(payload: { context: HTMLElement })} pause - Pauses bootstrap process.
 * @property {function(payload: { context: HTMLElement })} resume - Resumes bootstrap process.
 * @example
 * import { initialize } from 'core/bootstrap';
 *
 * // initialize bootstrap process
 * document.addEventListener( 'DOMContentLoaded', () => {
 *  initialize();
 * } );
 */

import { createInstance } from 'core/modules/Controller';
import { CONTROLLER_ELEM_ATTR } from 'core/constants';

/*+++++++++++++++++++++++++++++++++++*/
import 'controllers'; // load all controllers
/*+++++++++++++++++++++++++++++++++++*/

// state of bootstrap
let bootstrapping = false;

// list of HTML elements with `data-controller`
let controllerDirectives = [];

// counter for number of controller instances creates
let instCount = 0;

// controllers creation pause
let isPaused = false;

// while bootstrapping, if manual `run` request is received,
// push that request in the payloadQueue.
const payloadQueue = [];


/*********************************************************/


/**
 * Search controller directives in context of a HTML element
 * and store inside `controllerDirectives` array.
 */
const findControllerDirectives = ( context ) => {
    controllerDirectives = context.querySelectorAll( `[${ CONTROLLER_ELEM_ATTR }]` );
};

/**
 * From each controller directives in `controllerDirectives`,
 * create an instance of the controller.
 */
const initControllers = () => {
    for ( instCount = 0; instCount < controllerDirectives.length && ! isPaused ; instCount = instCount + 1 ) {
        const controllerName = controllerDirectives[ instCount ].getAttribute( CONTROLLER_ELEM_ATTR );
        createInstance( controllerName, controllerDirectives[ instCount ], { /* no HTML attributes passed */ } );
    }
};

// start bootstrap process
const bootstrap = ( context = document.body ) => {
    
    // search controllers in give element context and save
    findControllerDirectives( context );

    // initialize controllers
    initControllers();
};

// check tasks in `payloadQueue` and call `run` method with payload
const runWithPayloadQueue = () => {
    if ( 0 < payloadQueue.length ) {
        
        // run with payload
        run( payloadQueue.splice( 0, 1 )[ 0 ] );
    }
};

/**
 * @ignore
 * This will call bootstrap and will pass the context
 * in the bootstrap as an argument if context is defined.
 */
export const run = ( payload ) => {
    if ( true === bootstrapping ) {
        payloadQueue.push( payload );
    } else {
        bootstrapping = true;

        if ( 'undefined' === typeof payload || 'undefined' === typeof payload.context ) {
            bootstrap();
        } else {
            bootstrap( payload.context );
        }

        bootstrapping = false;
        
        // call `run` with payload queue
        runWithPayloadQueue();
    }
};


/**********************************************/


/**
 * @ignore
 */
// initialize bootstrap process
export const initialize = ( context ) => {
    run( context );
};

/**
 * @ignore
 */
// pause controllers creation
export const pause = () => {
    isPaused = true;
};

/**
 * @ignore
 */
// resume controllers creation
export const resume = () => {
    isPaused = false;

    for ( ; instCount < controllerDirectives.length && ! isPaused ; instCount = instCount + 1 ) {
        const controllerName = controllerDirectives[ instCount ].getAttribute( CONTROLLER_ELEM_ATTR );
        createInstance( controllerName, controllerDirectives[ instCount ], { /* no HTML attributes passed */ } );
    }
};
